<template>
  <div ref="mapContainer" class="map-container"></div>
</template>

<script>
export default {
  name: 'BaiduMap',
  props: {
    center: {
      type: Object,
      default: () => ({ lng: 116.404, lat: 39.915 })
    },
    zoom: {
      type: Number,
      default: 15
    }
  },
  data() {
    return {
      map: null
    };
  },
  mounted() {
    // 等待DOM更新后初始化地图
    this.$nextTick(() => {
      this.initMap();
    });
  },
  methods: {
    initMap() {
      // 检查百度地图API是否已加载
      if (typeof BMapGL === 'undefined') {
        console.error('Baidu Map API 未加载，请检查 index.html 中的配置');
        return;
      }

      // 初始化地图
      this.map = new BMapGL.Map(this.$refs.mapContainer);
      const point = new BMapGL.Point(this.center.lng, this.center.lat);
      this.map.centerAndZoom(point, this.zoom);
      this.map.enableScrollWheelZoom(true); // 启用滚轮缩放
      this.map.addControl(new BMapGL.NavigationControl()); // 添加导航控件
    },

    // 地址解析函数
    decodeAddress(address, city = '北京市') {
      return new Promise((resolve, reject) => {
        // 检查百度地图API是否已加载
        if (typeof BMapGL === 'undefined') {
          reject(new Error('Baidu Map API 未加载'));
          return;
        }

        const myGeo = new BMapGL.Geocoder();
        myGeo.getPoint(address, (point) => {
          if (point) {
            resolve(point);
          } else {
            reject(new Error('地址解析失败'));
          }
        }, city);
      });
    },

    // 根据地址定位地图
    locateByAddress(address, city = '北京市') {
      this.decodeAddress(address, city)
        .then(point => {
          this.map.centerAndZoom(point, 16);
          // 添加标记
          const marker = new BMapGL.Marker(point, { title: address });
          this.map.addOverlay(marker);
        })
        .catch(error => {
          console.error('定位失败:', error.message);
          alert('地址解析失败，请检查地址是否正确');
        });
    },
    // 根据经纬度设置地图中心点
    locateByCenter(lng, lat) {
      const point = new BMapGL.Point(lng, lat);
      this.map.centerAndZoom(point, this.zoom);
    },
    locateByGeoLocation(){
      var geolocation = new BMapGL.Geolocation();
      geolocation.getCurrentPosition((r)=>{
        if(geolocation.getStatus()==BMAP_STATUS_SUCCESS){
          var mk = new BMapGL.Marker(r.point);
          this.map.addOverlay(mk);
          this.map.panTo(r.point);
          return true;
        }
      })
      return false;
    },
    navigateByPoint(src_point, dst_point) {
      var driving = new BMapGL.DrivingRoute(this.map, { renderOptions: { map: this.map, autoViewport: true } });
      driving.search(src_point, dst_point);
    },
    navigateByAddress(src_address, dst_address, src_city = '北京市', dst_city = '北京市') {
      Promise.all([
        this.decodeAddress(src_address, src_city),
        this.decodeAddress(dst_address, dst_city)
      ])
        .then(([src_point, dst_point]) => {
          // 两个地址都解析成功，进行导航
          this.navigateByPoint(src_point, dst_point);

          // 可选：将地图中心定位到起点和终点的中间位置
          const centerLng = (src_point.lng + dst_point.lng) / 2;
          const centerLat = (src_point.lat + dst_point.lat) / 2;
          this.locateByCenter(centerLng, centerLat);
        })
        .catch(error => {
          console.error('地址解析失败:', error.message);
          alert('地址解析失败，请检查地址是否正确');
        });
    }
  },

  beforeDestroy() {
    if (this.map) {
      // 清理地图实例
      this.map = null;
    }
  }
};
</script>

<style scoped>
.map-container {
  width: 100%;
  height: 100%;
  min-height: 200px;
}
</style>