<template>
  <div v-if="isVisible" class="modal-overlay" @click="closeModal">
    <div class="modal-content" @click.stop>
      <div class="modal-body">
        <p>{{ message }}</p>
      </div>
      <div class="modal-footer">
        <button @click="confirmAction" class="btn-confirm">OK</button>
        <button @click="closeModal" class="btn-cancel">Close</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Modal',
  data() {
    return {
      isVisible: false,  // 控制模态框显示与隐藏的状态
      message: 'Demo does not support current operator.' // 默认错误消息
    };
  },
  methods: {
    // 打开模态框
    showModal(message) {
      this.message = message || this.message;  // 动态更新消息
      this.isVisible = true;  // 打开模态框
    },
    // 关闭模态框
    closeModal() {
      this.isVisible = false;  // 关闭模态框
    },
    // 确认操作
    confirmAction() {
      this.closeModal(); // 确认后关闭模态框
    }
  }
};
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}
.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 300px;
}
.modal-footer {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.btn-confirm, .btn-cancel {
  padding: 8px 12px;
  border: none;
  background-color: #007BFF;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}
.btn-cancel {
  background-color: #6c757d;
}
</style>
