import { createRouter, createWebHistory } from 'vue-router'

            const routes = [
             {
              path: '/',
              name: 'default_page',
              redirect: '/PhotoLibrary'
            },
         {
              path: '/PhotoLibrary',
              name: 'PhotoLibrary',
              component: () => import('@/views/PhotoLibrary.vue')
            },
            {
              path: '/PhotoLibrary.vue',
              name: 'redirect_PhotoLibrary',
              redirect: '/PhotoLibrary'
            },
         {
              path: '/SearchScreen',
              name: 'SearchScreen',
              component: () => import('@/views/SearchScreen.vue')
            },
            {
              path: '/SearchScreen.vue',
              name: 'redirect_SearchScreen',
              redirect: '/SearchScreen'
            },
         {
              path: '/SearchResults',
              name: 'SearchResults',
              component: () => import('@/views/SearchResults.vue')
            },
            {
              path: '/SearchResults.vue',
              name: 'redirect_SearchResults',
              redirect: '/SearchResults'
            },
         {
              path: '/PhotoSelectionMode',
              name: 'PhotoSelectionMode',
              component: () => import('@/views/PhotoSelectionMode.vue')
            },
            {
              path: '/PhotoSelectionMode.vue',
              name: 'redirect_PhotoSelectionMode',
              redirect: '/PhotoSelectionMode'
            },
         {
              path: '/NewAlbumCreation',
              name: 'NewAlbumCreation',
              component: () => import('@/views/NewAlbumCreation.vue')
            },
            {
              path: '/NewAlbumCreation.vue',
              name: 'redirect_NewAlbumCreation',
              redirect: '/NewAlbumCreation'
            },
        
            ]

            const router = createRouter({
              history: createWebHistory(process.env.BASE_URL),
              routes
            })

            export default router